import React from 'react';

import User from './User';
import CommentList from './CommentList';

function Post({ post }) {
  return (
    <div>
      <User user={post.user} />
      <h2>{post.title}</h2>
      <p>{post.body}</p>
      <CommentList commentList={post.comments} />
    </div>
  );
}

export default Post;
